body {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  background: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  margin: 20px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

#habit-form {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

#habit-input {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  margin-bottom: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #28a745;
  border: none;
  border-radius: 4px;
  padding: 10px;
}

button:hover {
  background: #218838;
}

#habit-list {
  padding: 0;
  list-style: none;
}

.habit-item {
  border-bottom: 1px solid #ccc;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.habit-item:last-child {
  border-bottom: none;
}

.habit-item button {
  color: #fff;
  cursor: pointer;
  background: #dc3545;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
}

.habit-item button:hover {
  background: #c82333;
}

#stats {
  margin-top: 20px;
}

@media (width <= 600px) {
  .container {
    margin: 10px;
    padding: 15px;
  }

  h1 {
    font-size: 20px;
  }

  #habit-form {
    flex-direction: column;
  }

  #habit-input {
    margin-bottom: 10px;
  }

  button {
    width: 100%;
  }

  .habit-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .habit-item button {
    width: 100%;
    margin-top: 10px;
  }
}
